import { useFormik } from 'formik';
import * as Yup from 'yup';
import styles from './robotForm.module.css';


interface IFormValues {
  model: string;
  creator: string;
  email: string;
}

// *    
// .required() -     
// .number() -    ,   
// .string() -   
// .typeError('   ') -      
// .min(100, '   ') -  ,   
// .max(1000, '   ') -  ,   
// .email('   ') -   email

const schema = Yup.object().shape({
  model: Yup
    .number()
    .typeError('  ,  ')
    .required('  !')
    .min(100, '     100!')
    .max(1000, '   !   1000...'),
  creator: Yup
    .string()
    .required(',   '),
  email: Yup
    .string()
    .email('  email')
    .required('email   !'),
});


export default function RobotForm() {


  const formik = useFormik({
    initialValues: {
      model: '',
      creator: '',
      email: ''
    } as IFormValues,
    //     yup
    validationSchema: schema,
    //      submit
    validateOnChange: false,
    onSubmit: (values: IFormValues, { resetForm }) => {
      console.log(values);
      resetForm();
    }
  });


  return (
    <>
      <form onSubmit={formik.handleSubmit} className={styles.loginForm}>
        <label>   </label>
        <input value={formik.values.model} name='model' onChange={formik.handleChange} type="text" placeholder=' ' />
        <input value={formik.values.creator} name='creator' onChange={formik.handleChange} type="text" placeholder=' ' />
        <input value={formik.values.email} name='email' onChange={formik.handleChange} type="text" placeholder='email ' />
        <button type='submit'></button>
      </form>
      <span className={styles.errors}>{formik.errors.model}</span>
      <span className={styles.errors}>{formik.errors.creator}</span>
      <span className={styles.errors}>{formik.errors.email}</span>
    </>


  );
}